
header {
  border-bottom: 1px solid #ccc;
  /*margin-bottom: 1rem;*/
}
.header {
	margin: 0 auto;
	width: 90%;
}
header h1 {
	margin-left: 1rem;
	margin-bottom: .5rem;
	font-size: 1.2rem;
	color: #000;
	font-weight: 500;
}
.header-nav {
	margin: 1rem 0;
}
.header-nav-item {
	display: inline-block;
	width: 33.33%;
	font-size: .8rem;
	text-align: center;
}
.header-nav-item.active {
	font-weight: bold;
}



