
.theme-list-title, .theme-list-desc, .theme-list-editor, .theme-list-content{
	width: 80%;
	margin: 0 auto;
}
.theme-img img{
	width: 100%;
}
.theme-list-content {
	padding: 0;
}
.theme-list-title {
	font-size: 1rem;
	margin-top: 1rem;
}
.theme-list-desc {
	font-size: .5rem;
	color: #000;
	padding: 1rem;
}
.theme-list-desc span {
	padding-left: 1rem;
	border-left: 5px solid #ddd;
}
.theme-list-editor {
	font-size: .7rem;
	margin-bottom: .5rem;
}
.theme-list-item {
	list-style: none;
	font-size: .8rem;
	padding: .8rem 0;
	border-bottom: 1px solid #ddd;
}



