
.daily-date {
	font-size: .8rem;
	border-left: 5px solid #ddd;
	padding-left: 1rem;
	line-height: 1rem;
	height: 1rem;
	margin: .8rem .5rem .5rem;
	animation: slide 1s;
}
.daily {
	border-bottom: 1px solid #ddd;
}
@keyframes slide {
from {
		margin-left: 3rem;
		opacity: 0;
}
to {
		margin-left: .5rem;
		opacity: 1;
}
}



