
.theme-item {
	display: block;
	padding-bottom: .5rem;
	margin: .5rem .2rem 0;
	border-bottom: 1px solid #ddd;
}
.theme-item-img {
	width: 12%;
	float: left;
	margin: 0 .2rem
}
.theme-item-img img {
	width: 100%;
	display: block;
	border-radius: 50%;
}
.theme-item-title {
	font-size: .65rem;
}
.theme-item-desc {
	width: 85%;
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
	font-size: .6rem;
	color: #999;
}



